@import "common/mix";
@import "common/animation";
@import "common/typography";


html {
  box-sizing: border-box;
  height: 100%; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  @include roboto;
  height: 100%; }

.wrapper {
  position: relative;
  overflow: hidden;
  min-width: 320px;
  min-height: 100%;
  color: $primaryClr;
  background-color: $bgClr; }


.person {
  transform-style: preserve-3d;
  text-align: center;
  color: $baseClr;

  &-imgWrap {
    overflow: hidden;
    width: 141px;
    height: 141px;
    margin: 0 auto 20px;
    border-radius: 50%; }

  &-img {
    max-width: 100%;
    height: auto;
    background-color: $theme; }

  &-title {
    margin: 0;
    margin-bottom: 6px;
    font-size: 21px;
    font-weight: 500;
    line-height: 1.33; }

  &-txt {
    margin-bottom: 34px;
    opacity: 0.8;
    font-size: 14px; } }


.preloader {
  position: fixed;
  z-index: 50;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: darken(rgba($theme, 0.87), 20%);

  &-inner {
    position: relative;
    width: 100px;
    height: 100px; }

  &-noJs {
    display: none; } }


.circle {
  position: relative;
  display: block;
  width: 100px;
  height: 100px;

  &-small {
    transform-origin: 50px 50px;
    @include spin;
    fill: $baseClr; }

  &-txt {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $baseClr;
    font-size: 18px; }

  &-bar {
    transition: all 1s ease-in-out;
    stroke-dashoffset: 316px;
    stroke-dasharray: 316px;
    stroke: $baseClr; } }


.formError {
  animation: shake 0.6s ease; }
.fieldValid {
  box-shadow: 0 0 5px 3px $valid; }
.fieldError {
  box-shadow: 0 0 5px 3px $error; }


.moveUp {
  transform: translate3d(0, 0, 0);
  opacity: 1;
  transition: all 1.2s ease;

  &--hidden {
    transform: translate3d(0, 500px, 0);
    opacity: 0; } }


.arrowUp {
  display: block;
  width: 48px;
  height: 48px;
  margin: 40px auto;
  text-align: center;

  &-icon {
    width: 26px;
    height: 17px;
    fill: $theme;
    @include arrow; }

  &:hover .arrowUp-icon {
    fill: darken($theme, 15%); }

  &:active .arrowUp-icon {
    fill: darken($theme, 15%); } }






@import "welcome/welcome";
@import "common/menu";
@import "common/header/header";
@import "common/footer/footer";
@import "blog/blog";
@import "about/about";
