// SANSUS-WEBISSIMO
//=======================================================
@font-face {
  font-family: 'sansus_webissimoregular';
  font-weight: normal;
  font-style: normal;
  src: url('../fonts/sansus-webissimo-regular-webfont.woff2') format('woff2'), url('../fonts/sansus-webissimo-regular-webfont.woff') format('woff'); }



@mixin roboto($weight: 400, $size: 16px, $height: 1, $color: $baseClr) {
  color: $color;
  font-family: Roboto, Arial, sans-serif;
  font-size: $size;
  font-weight: $weight;
  font-style: normal;
  line-height: $height; }

@mixin sansus($size: 16px, $height: 1, $color: $primaryClr) {
  color: $color;
  font-family: sansus_webissimoregular, arial, sans-serif;
  font-size: $size;
  font-weight: normal;
  font-style: normal;
  line-height: $height; }
